<template>
  <!--  -->
  <div class="border-bottom my-2 pb-2">
    <h2 class="h5"></h2>
    <label for=""></label>
    <input type="text" v-model="scoreCap" id="" style="width: 35px">

    <span class="ms-3" :class="{'text-danger': allotScore>scoreCap}">{{ allotScore }} / {{ scoreCap }}</span>
  </div>
  <br>

  <h2 class="h5"></h2>
  <!--  -->
  <label for=""></label>
  <input type="text" id="" v-model="scorePercentage." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">
  <label for=""></label>
  <input type="text" id="" v-model="questionsNumList." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">

  <br>
  <label for=""></label>
  <input type="text" id="" v-model="scorePercentage." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">
  <label for=""></label>
  <input type="text" id="" v-model="questionsNumList." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">

  <br>
  <label for=""></label>
  <input type="text" id="" v-model="scorePercentage." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">
  <label for=""></label>
  <input type="text" id="" v-model="questionsNumList." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">

  <br>
  <label for=""></label>
  <input type="text" id="" v-model="scorePercentage." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">
  <label for=""></label>
  <input type="text" id="" v-model="questionsNumList." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">

  <br>
  <label for=""></label>
  <input type="text" id="" v-model="scorePercentage." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">
  <label for=""></label>
  <input type="text" id="" v-model="questionsNumList." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">

  <br>
  <label for=""></label>
  <input type="text" id="" v-model="scorePercentage." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">
  <label for=""></label>
  <input type="text" id="" v-model="questionsNumList." style="width: 30px" oninput="value=value.replace(/[^\d]/g,'')">

  <br>

  <ul class="my-2">
    <template v-for="(val, keys) in questionsList" :key="keys">
      <li v-if="val.queNum && val.score">{{ keys }}{{ val.queNum }} ,  {{ val.score }} </li>
    </template>
  </ul>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {

  computed: {
    ...mapState(['questionsList']),
    //* 
    allotScore () {
      let useScore = 0 //* 
      Object.keys(this.scorePercentage).forEach(score => {
        const queScore = this.scorePercentage[score] //* 
        const queNum = this.questionsNumList[score] //* 
        if (queScore > 0) {
          useScore += queScore * queNum
        }
        return useScore
      })

      return useScore
    }
  },

  watch: {
    //* :"""" =>  Store
    questionsNumList: {
      handler () {
        const obj = {}
        Object.keys(this.questionsNumList).forEach(keys => {
          const score = parseInt(this.scorePercentage[keys])
          const queNum = parseInt(this.questionsNumList[keys])
          //* """" Store
          if (score && queNum) {
            obj[keys] = {
              queNum,
              score
            }
          } else {
            obj[keys] = {
              queNum: 0
            }
          }
        })
        this.GET_QUESTIONS_NUM(obj)
      },
      deep: true
    },
    //* :"""" =>  Store
    scorePercentage: {
      handler (val) {
        const obj = {}
        Object.keys(val).forEach(keys => {
          const score = parseInt(this.scorePercentage[keys])
          const queNum = parseInt(this.questionsNumList[keys])
          //* """" Store
          if (score && queNum) {
            obj[keys] = {
              queNum,
              score
            }
          } else {
            obj[keys] = {
              queNum: 0
            }
          }
        })
        this.GET_QUESTIONS_NUM(obj)
      },
      deep: true
    }
  },

  data () {
    return {
      scoreCap: 100,
      scorePercentage: {
        : 0,
        : 0,
        : 0,
        : 0,
        : 0,
        : 0
      },
      questionsNumList: {
        : 0,
        : 0,
        : 0,
        : 0,
        : 0,
        : 0
      }
    }
  },

  methods: {
    ...mapMutations(['GET_QUESTIONS_NUM'])
  }

}
</script>
