<template>
  <ul>
    <li v-for="question in questions" :key="question.">
      {{ question. }}
      {{ randomSort(question.) }}
    </li>
  </ul>
  <button type="button" @click="randomSortQuestion"></button>
</template>

<script>

export default {
  components: {
  },

  data () {
    return {
      questions: [
        {
          : '1',
          : '',
          : ['X', 'X', '', 'X'],
          : 'A'
        },
        {
          : '2',
          : '',
          : ['X', 'X', '', 'X'],
          : 'B'
        },
        {
          : '3',
          : '',
          : ['X', 'X', '', 'X'],
          : 'D'
        },
        {
          : '4',
          : '',
          : ['X', 'X', '', 'X'],
          : 'A'
        },
        {
          : '5',
          : '',
          : ['X', 'X', '', 'X'],
          : 'C'
        }
      ]
    }
  },

  watch: {
  },

  methods: {
    randomSort (arr) {
      for (let i = 0, l = arr.length; i < l; i++) {
        const rc = parseInt(Math.random() * l)
        // 
        const empty = arr[i]
        arr[i] = arr[rc]
        arr[rc] = empty
      }
      return arr
    },
    randomSortQuestion () {
      this.randomSort(this.questions)
    }
  },

  mounted () {
  }

}
</script>

<style lang='scss' scope></style>
