<template>
  <!--  -->
  <h2 class="h5"></h2>
   <input type="number" v-model="examPaperNum" style="width:45px"> 
  <button type="button" @click="getExamPaper"></button>

  <br>
  <br>

  <!-- : -->
  <select @change="changePreviewExPaper" v-if="Object.keys(teacherUseExPaper).length===0 && Object.keys(previewQueList).length>0">
    <option :value="keys" v-for="(val,keys) in previewQueList" :key="`perview${keys}`">
      {{ keys }}
    </option>
  </select>

  <button type="button" class="mx-5 my-4" @click="confirmExPaper" v-if="Object.keys(teacherUseExPaper).length===0&& Object.keys(previewQueList).length>0"></button>
  <button type="button" class="mx-5 my-4" @click="resetExPaper" v-if="Object.keys(teacherUseExPaper).length>0"></button>

  <br>

  <!-- : -->
  <ul class="mb-2" v-if="Object.keys(teacherUseExPaper).length===0">
    <li v-for="(val, keys) in perviewExamPaper" :key="keys">
      <p class="mb-0">
        {{ keys }} <small>( {{ questionsList[keys].score }} ,  {{ val.length }})</small>
      </p>
      <ul>
        <li v-for="(val) in val" :key="val">
          :{{ val. }}, <span class="bg-hightLight">{{ getAnsPosition(val.) }}</span>, {{ val. }}
        </li>
      </ul>
    </li>
  </ul>

  <!--  -->
  <button type="button" @click="randomSort" v-if="Object.keys(teacherUseExPaper).length>0"></button>
  <ul class="mb-2" v-if="Object.keys(teacherUseExPaper).length>0">
    <li v-for="(val, keys) in teacherUseExPaper" :key="keys">
      <p class="mb-0">
        {{ keys }} <small>( {{ questionsList[keys].score }} ,  {{ val.length }})</small>
        <span class="bg-hightLight ms-5">{{ ansPercentage(val) }}</span>
      </p>
      <ul>
        <li v-for="(val) in val" :key="val">
          :{{ val. }}, <span class="bg-hightLight">{{ getAnsPosition(val.) }}</span>, {{ val. }}
        </li>
      </ul>
    </li>
  </ul>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import QuestionsView from '@/mixins/QuestionsBank.vue'
export default {

  mixins: [QuestionsView], //* 

  computed: {
    ...mapState(['questionsList'])
  },

  data () {
    return {
      previewQueList: {}, //* 
      perviewExamPaper: {},
      examPaperNum: 1,
      teacherUseExPaper: {} //* 

    }
  },

  methods: {
    ...mapMutations(['CONFIRM_EX_PAPER', 'RESET_EX_PAPER']),

    //* 
    changePreviewExPaper (e) {
      const currentExPaper = e.target.value
      this.perviewExamPaper = this.previewQueList[currentExPaper]
    },
    //* *
    getExamPaper () {
      //*  0
      const exPaperNum = this.examPaperNum
      if (exPaperNum > 0) {
        this.previewQueList = {} //* 
        for (let i = 0; i < exPaperNum; i++) {
          this.previewQueList[`${i + 1}`] = this.getRandomQuestions()
        }
        this.perviewExamPaper = this.previewQueList['1'] //* 1
      }
    },
    //* 
    resetExPaper () {
      //* 
      this.teacherUseExPaper = {}
      this.previewQueList = {}
      this.perviewExamPaper = {}
      this.RESET_EX_PAPER()
    },
    //*  
    getRandomQuestions () {
      const obj = {}
      Object.keys(this.questionsList).forEach((queKeys) => {
        //*  0
        if (this.questionsList[queKeys].queNum) {
          const queNum = this.questionsList[queKeys].queNum
          const shuffled = this.questions[queKeys].slice(0)
          let i = this.questions[queKeys].length
          const min = i - queNum
          let temp
          let index
          while (i-- > min) {
            index = Math.floor((i + 1) * Math.random())
            temp = shuffled[index]
            shuffled[index] = shuffled[i]
            shuffled[i] = temp
          }
          obj[queKeys] = shuffled.slice(min)
        }
      })
      return obj
    },
    //* 
    randomSort () {
      const that = this
      let newArr = []
      Object.keys(this.teacherUseExPaper).forEach(keys => {
        this.teacherUseExPaper[keys].forEach(item => {
          const arr = []
          Object.keys(item.).forEach(keys => {
            arr.push(item.[keys])
            newArr = sort(arr)
          })

          item. = {
            A: newArr[0],
            B: newArr[1],
            C: newArr[2],
            D: newArr[3]
          }
        })
      })
      //* 
      function sort (arr) {
        for (let i = 0, l = arr.length; i < l; i++) {
          const rc = parseInt(Math.random() * l)
          // 
          const empty = arr[i]
          arr[i] = arr[rc]
          arr[rc] = empty
        }
        return arr
      }
      this.getAnswerIndex() //* 
      that.CONFIRM_EX_PAPER(that.perviewExamPaper)
    },
    //* 
    getAnsPosition (ansObj) {
      const ansOptions = ['A', 'B', 'C', 'D']
      const position = Object.keys(ansObj).findIndex(keys => {
        return ansObj[keys] === ''
      })
      return ansOptions[position]
    },
    //* 
    ansPercentage (ans) {
      const answerCount = {
        A: 0,
        B: 0,
        C: 0,
        D: 0
      }
      ans.forEach(item => {
        const ansIndex = Object.keys(item.).findIndex(optionABCD => {
          return item.[optionABCD] === ''
        })
        if (ansIndex === 0) {
          answerCount.A++
        } else if (ansIndex === 1) {
          answerCount.B++
        } else if (ansIndex === 2) {
          answerCount.C++
        } else if (ansIndex === 3) {
          answerCount.D++
        }
      })
      return `A${answerCount.A}, B${answerCount.B}, C ${answerCount.C}, D${answerCount.D}`
    },
    //* 
    confirmExPaper () {
      this.teacherUseExPaper = this.perviewExamPaper
      this.getAnswerIndex() //* 
      this.CONFIRM_EX_PAPER(this.perviewExamPaper)
    },
    //* 
    getAnswerIndex () {
      Object.keys(this.teacherUseExPaper).forEach(key => {
        this.teacherUseExPaper[key].forEach(items => {
          const ansIndex = Object.keys(items.).findIndex(keys => {
            return items.[keys] === ''
          })
          //* 
          items. = ansIndex
        })
      })
    }
  },

  mounted () {
    //* 
    const answerCount = {
      A: 0,
      B: 0,
      C: 0,
      D: 0
    }
    //* :
    const data = []
    function random (type, num) {
      for (let i = 0; i < num; i++) {
        const obj = {
          : `${type} ${i + 1}`,
          : {
            A: ' 1',
            B: ' 2',
            C: ' 3',
            D: ''
          },
          : getRandomAns()
        }
        data.push(obj)
      }
      // console.log(data)
      // console.log(answerCount) //* 
    }
    random('', 100) // ?  100  

    //*  A-D
    function getRandomAns () {
      const answerList = ['A', 'B', 'C', 'D']
      const answer = answerList[Math.floor(Math.random() * answerList.length)]
      answerCount[answer]++

      return answer
    }
  }

}
</script>

<style lang='scss' scope>
.bg-hightLight{
  background: rgba(189, 253, 255, 0.665);
}
</style>
